import React, { useEffect, useState } from 'react'
import { useParams } from "react-router-dom";
import TitleCard from "../../../components/Cards/TitleCard";
import InputText from "../../../components/Input/InputText";
import { useDispatch, useSelector } from "react-redux";
import { fetchMemberDetailAction, fetchVerificationMemberDetail } from "../../../app/service/action/memberAction";
import Loading from "../../../components/Loading/Loading";
import TextAreaInput from "../../../components/Input/TextAreaInput";
import { errorAlert, successAlert } from "../../../app/service/helper/handlingError";
import SelectBox from "../../../components/Input/SelectBox";
import { INITIAL_OBJECT } from "./component/interface";
import { DATA_OPTION_GENDER, DATA_OPTION_JERSEY } from "../../../utils/dummyData";
import { InputFileImage } from "../../../components/Input/InputFileImage";
import { fetchAllMasterCategoryRaceAction } from "../../../app/service/action/categoryRaceAction";
import { errorImage } from "../../../utils/Utils";

const RegistrationDetail = () => {
    const { id, status } = useParams();
    const dispatch = useDispatch()
    const [requestParam, setRequestParams] = useState(INITIAL_OBJECT)
    const [loading, setLoading] = useState(true);
    const [dataOptionCategory, setDataOptionCategory] = useState([]);

    const updateFormValue = ({ updateType, value }) => {
        // setErrorMessage("")
        setRequestParams({ ...requestParam, [updateType]: value })
    }

    const updateImageValue = ({ updateType, value }) => {
        // setErrorMessage("")
        setRequestParams({ ...requestParam, [updateType]: value })
    }

    const statusCategory = useSelector(({ masterCategoryRaceState }) => {
        return masterCategoryRaceState.masterCategoryRace.status
    });

    const data = useSelector(({ masterCategoryRaceState }) => {
        return masterCategoryRaceState.masterCategoryRace.data;
    })

    useEffect(() => {
        if (statusCategory === "ready" && data.result) {
            const update = data.result.map((item) => ({
                value: item.id,
                name: item.name
            }))
            setDataOptionCategory(update)
        }
    }, [data.result, status, statusCategory]);

    useEffect(() => {
        // @ts-ignore
        dispatch(fetchAllMasterCategoryRaceAction())
    }, [dispatch]);

    useEffect(() => {
        dispatch(fetchMemberDetailAction(id))
            .then((response) => {
                if (response.responseData.code === 200 && response.result) {
                    setRequestParams(response.result)
                    setLoading(false)
                }
            })
            .catch(error => errorAlert(error))
    }, [dispatch, id]);

    const handleSubmit = (status) => {

        dispatch(fetchVerificationMemberDetail(requestParam, id, status))
            .then(response => {
                console.log(response)
                if (response.result && response.responseData.code === 200) {
                    successAlert("update data success")
                    setTimeout(() => {
                        if (status === 'verification') {
                            window.location.href = '/app/member/registration'
                        } else {
                            window.location.reload()
                        }
                    }, 2000)
                }
            })
            .catch(error => errorAlert(error))
    }

    return (
        <>
            <TitleCard title={"Data Rider"} topMargin={'mt-2'}>
                {loading ? <Loading /> :
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <InputText labelTitle="Nama Lengkap" updateType={'fullname'}
                            defaultValue={requestParam.fullname}
                            updateFormValue={updateFormValue} />
                        <InputText labelTitle="Nama Panggilan" updateType={'nickname'}
                            defaultValue={requestParam.nickname}
                            updateFormValue={updateFormValue} />
                        <SelectBox labelTitle='Kategori' options={dataOptionCategory}
                            updateType={'masterCategoryRaceId'}
                            defaultValue={requestParam.masterCategoryRaceId}
                            updateFormValue={updateFormValue} />
                        <InputText type='date' labelTitle="Tanggal Lahir" updateType={'dob'}
                            defaultValue={requestParam.dob}
                            updateFormValue={updateFormValue} />
                        <SelectBox labelTitle='Jenis Kelamin' options={DATA_OPTION_GENDER}
                            updateType={'gender'}
                            defaultValue={requestParam.gender}
                            updateFormValue={updateFormValue} />
                        <SelectBox labelTitle='Ukuran Jersey' options={DATA_OPTION_JERSEY}
                            updateType={'sizeJersey'}
                            defaultValue={requestParam.sizeJersey}
                            updateFormValue={updateFormValue} />
                        <InputText labelTitle="Jersey Number" updateType={'jerseyNumber'}
                            defaultValue={requestParam.jerseyNumber}
                            updateFormValue={updateFormValue} />
                        <InputText labelTitle="Member ID" updateType={'memberId'} disabled={true}
                            defaultValue={requestParam.memberId}
                            updateFormValue={updateFormValue} />
                        <div className={'image'}>
                            <img src={requestParam.photoRider} alt="" onError={errorImage} />
                        </div>
                        <InputFileImage labelTitle={'Foto Rider'} updateType={'photoRider'}
                            updateFormValue={updateImageValue} />
                        <div className={'image'}>
                            <img src={requestParam.certificateBirtDay} alt="" onError={errorImage} />
                        </div>
                        <InputFileImage labelTitle={'Foto Akte'} updateType={'certificateBirtDay'}
                            updateFormValue={updateImageValue} />
                    </div>
                }
            </TitleCard>
            <TitleCard title={'Data Orang Tua'}>
                {loading ? <Loading /> :
                    <div>
                        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                            <InputText labelTitle="Nama Ayah" defaultValue={requestParam.fatherName}
                                updateType={'fatherName'}
                                updateFormValue={updateFormValue} />
                            <InputText labelTitle="HP/Whatsapp Ayah" defaultValue={requestParam.fatherPhone}
                                updateType={'fatherPhone'}
                                updateFormValue={updateFormValue} />
                            <InputText labelTitle="Nama Ibu" defaultValue={requestParam.motherName}
                                updateType={'motherName'}
                                updateFormValue={updateFormValue} />
                            <InputText labelTitle="HP/Whatsapp Ibu" defaultValue={requestParam.motherPhone}
                                updateType={'motherPhone'}
                                updateFormValue={updateFormValue} />
                            <TextAreaInput labelTitle="Alamat" defaultValue={requestParam.address}
                                updateType={'address'}
                                updateFormValue={updateFormValue} />
                            <InputText labelTitle="Email" defaultValue={requestParam.email}
                                updateType={'email'}
                                updateFormValue={updateFormValue} />
                        </div>
                    </div>
                }
            </TitleCard>
            <TitleCard title={'Pembayaran'}>
                {loading ? <Loading /> :
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                        <div className={'image'}>
                            <img src={requestParam.transferImage} alt="" onError={errorImage} />
                        </div>
                        <InputFileImage labelTitle={'Bukti Pembayaran'} updateType={'transferImage'}
                            updateFormValue={updateImageValue} />
                    </div>
                }
            </TitleCard>
            <div className={'button'}>
                <button className={'btn btn-primary'} onClick={() => handleSubmit(status)}>
                    {status === 'verification' ? 'VERIFIKASI' : 'UPDATE'}
                </button>
            </div>
        </>
    )
}

export default RegistrationDetail;
